import React from 'react';
import './home.css';
import dp from '../../assets/dp.png';

const Home = () => {
  return (
    <section id='home'>
    <div className='homeText'>
    
        <div className='homePara'>
        <p><span className='hello'>Hello,</span><br></br>I'm a computer engineer and have been a <span className='span1'>Java developer</span> for the past three years.</p>
        <p>I value optimism, grit, wisdom, and empathy. <br></br></p>
        <p>Apart from coding, I enjoy reading and resistance training. 
         <br></br>I also maintain defined <span className='span1'>abs</span> and can bench press <span className='span2'>225lbs</span>.</p>
        <hr className='line'></hr>
        <p className='arrow'>scroll to learn more <span>&#8595;</span></p>
        </div>
        <h1 className='name'>Rohan<br></br> Beldar</h1>

    </div>
    <div>
    
    <img src={dp} alt='Profile' className='dp'></img>

    </div>
    </section>
  )
}

export default Home