import React, {useRef} from 'react'
import './contact.css'
import Skyline from '../../assets/skyline.jpg'; 
import emailjs from '@emailjs/browser';


const Contact = () => {

  const form = useRef();
 
  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm('service_jz2p6sm', 'template_8tlwyxf', form.current, 'vq9C_cuy8NxLneELf')
      .then((result) => {
          console.log(result.text);
          e.target.reset();
          alert('You broke the ice!');
      }, (error) => {
          console.log(error.text);
      });
  };

  return (
   
    <section id='contact'>
    <div>
      <h2 className='title'>Get in touch...</h2>
    </div>
    <div className='formnimg'>
      <div className='form'>
        <form className='contactform' ref={form} onSubmit={sendEmail}>
          <input type='text'className='formname' placeholder='What is your name?' name='from_name'/>
          <input type='email'className='email' placeholder='And your email address?'name='your_email'/>
          <textarea className='msg' placeholder='How can I help you?' name='message'/>
          <button type='submit' value='send' className='submitbtn'><span className='btnspan'>Send it</span></button>
        </form>
      </div>
      <div className='skyline'>
        <img src={Skyline} alt='skyline'></img>
      </div>
    </div>
    
    <div className='icons'>
      <hr className='iconline'></hr>
    <div className='iconrow'>
    <a href={"https://www.linkedin.com/in/rohan-beldar-147885203"} target="_blank" rel="noopener noreferrer" class="fa fa-linkedin icon"></a>
    <a href="https://github.com/belyeah" target="_blank" class="fa fa-github icon"></a>

    <a href="https://www.instagram.com/bel__yeah" target="_blank" class="fa fa-instagram icon"></a>
    </div>
    <p className='copyright'>&copy; 2023 Rohan V. Beldar</p>

    </div>
    </section>
  )
}



export default Contact