import React from 'react'
import './journey.css'

const journey = () => {
  return (
    <section id='journey'>
    <div className='journeytitle'>My journey</div>
    <div className='cards'>

        <div className='card'>
            <hr className='cardline'></hr>
            <h4 className='location'>Toronto</h4>
            <div className='cardtextall'>
            <p>Java Developer at Tech Mahindra</p>
            <p className='cardtext'>Oracle certified professional: java SE 17</p>
            <p className='cardtext'>AWS certified cloud practitioner</p>
            </div>
            

        </div>
        <div className='card waterloo'>
            <hr className='cardline'></hr>
            <h4 className='location'>Waterloo</h4>
            <div className='cardtextall'>
            <p>Post-Grad diploma, Conestoga College '22</p>
            <p className='cardtext'>Mobile Solutions Development</p>
            <p className='cardtext'>Voluntary work and capstone project</p>
            </div>
        </div>
        <div className='card'>
            <hr className='cardline'></hr>
            <h4 className='location'>Pune</h4>
            <div className='cardtextall'>
            <p>Bachelor of engineering, Pune University '19</p>
            <p className='cardtext'>Internship at gathrr.in</p>
            <p className='cardtext'>Java Developer at Phoenixgen Systems</p>
        </div>
        </div>
        
    </div>
    </section>
  )
}

export default journey